<template>
	<div class="service">
		<div class="e" @click="changeService('experience', service[0])">
			<img :src="experience">
		</div>
		<div class="w50 c" @click="changeService('cosmetology', service[1])">
			<img :src="cosmetology">
		</div>
		<div class="w50 r" @click="changeService('regenerate', service[2])">
			<img :src="regenerate">
		</div>
	</div>
</template>

<script>
	export default {
		props: ['data'],
		data() {
			return {
				service: [],
				experience: '',
				cosmetology: '',
				regenerate: ''
			}
		},
		methods: {
			changeService(path, item) {
				this.$emit('changeService', { path: path, item: item });
			}
		},
		watch: {
			data(newValue) {
				this.service = this.data;
				this.experience = this.service[0].headimg;
				this.cosmetology = this.service[1].headimg;
				this.regenerate = this.service[2].headimg;
			}
		}
	}
</script>

<style lang="scss">
	@import '@/styles/components/service.scss';
</style>