import axios from 'axios'
import qs from 'qs'

axios.defaults.timeout = 300000;
axios.interceptors.response.use(
	response => {
		return response.status === 200 ? Promise.resolve(response) : Promise.reject(response);
	},
	error => {
		if (error.response && error.response.data && error.response.data.code) {
			// 返回请求错误处理
			console.log(error.response.data);
		}
		return Promise.reject(error);
	}
)

const http = {
	get: (url, params) => {
		let _params = {}, _headers = {};

		if (params) params.data ? (_params = _params.data, _headers = params.headers) : _params.headers;
		if (_headers) axios.defaults.headers.post['Content-Type'] = _headers['Content-Type'] || 'application/x-www-form-urlencoded';
		// if (sessionStorage.token && !axios.defaults.headers.common['Authorization']) axios.defaults.headers.common['Authorization'] = sessionStorage.token;

		return new Promise((resolve, reject) => {
			axios.get(
				url + qs.stringify(_params, { arrayFormat: 'repeat', addQueryPrefix: true })
			).then(response => {
				resolve(response);
			}).catch(error => {
				reject(error);
			})
		})
	},
	post: (url, params) => {
		let _params = {}, _headers = {};

		if (params) params.data ? (_params = _params.data, _headers = params.headers) : _params.headers;
		if (_headers && _headers['Content-Type']) {
			axios.defaults.headers.post['Content-Type'] = _headers['Content-Type'];

			if (_headers['Content-Type'] == 'application/json;charset=UTF-8') _params = JSON.stringify(_params);
		}else{
			axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
			_params = qs.stringify(_params);
		}
		// if (sessionStorage.token && !axios.defaults.headers.common['Authorization']) axios.defaults.headers.common['Authorization'] = sessionStorage.token;

		return new Promise((resolve, reject) => {
			axios.post(url, _params).then(response => {
				resolve(response);
			}).catch(error => {
				reject(error);
			})
		})
	}
}

export default http