<template>
	<div class="card" @click="changeExample">
		<div class="card-cover">
			<img :src="`${publicPath + caseSource.headImg}`">
		</div>
		<div class="card-body">
			<div class="card-meta-title">{{caseSource.title}}</div>
			<p :key="index" v-for="(detail, index) in caseSource.details">{{detail}}</p>

			<div class="card-meta-tags btn-tags" v-if="caseSource.name || caseSource.tags.length > 0">
				<div><p>{{caseSource.tip}}</p></div>
				<div :key="tag.status" v-for="tag in caseSource.tags" :class="['btn-tag', 'color' + tag.status]">{{tag.title}}</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: ['data'],
		data() {
			return {
				caseSource: {},
				publicPath: process.env.BASE_URL,
			}
		},
		created() {
			this.caseSource = this.data;
		},
		methods: {
			changeExample() {
				this.$emit('changeExample', this.caseSource)
			}
		}
	}
</script>

<style lang="scss">
	@import '@/styles/components/card.scss';
</style>