<template>
	<div id="app">
		<Titlebar />
		
		<div class="page-content" :class="{'page-content-tabbar': tabbarVisible}">
			<transition :name="transitionDirection" mode="out-in" @before-enter="afterEnter" appear>
				<router-view :key="$route.fullPath" @handlerTabbar="handlerTabbar" @handlerLoading="handlerLoading" />
			</transition>
		</div>
		
		<Contact :style="{ marginBottom: tabbarVisible ? '49px' : 0 }" />
		<Tabbar v-if="tabbarVisible" />

		<div class="ant-spin-loading ant-spin ant-spin-spinning" v-if="loading" ontouchmove="return false" :style="{ height: antSpinHeight }">
			<span class="ant-spin-dot ant-spin-dot-spin" :style="{ marginTop: antSpinDotTop }">
				<i></i><i></i><i></i><i></i>
			</span>
		</div>
	</div>
</template>

<script>
	import Titlebar from '@/components/Titlebar.vue'
	import Contact from '@/components/Contact.vue'
	import Tabbar from '@/components/Tabbar.vue'

	export default {
		name: 'app',
		components: { Titlebar, Contact, Tabbar },
		data() {
			return {
				transitionDirection: 'slide-left-',
				tabbarVisible: JSON.parse(sessionStorage.getItem('tabbarVisible')),
				loading: false,
				antSpinHeight: 0,
				antSpinDotTop: 0
			}
		},
		created() {
			this.antSpinHeight = window.innerHeight + 'px';
			this.antSpinDotTop = window.innerHeight/2 + 'px';
		},
		methods: {
			handlerTabbar(res) {
				this.tabbarVisible = res;
			},
			afterEnter() {
				this.$root.$emit('scrollAfterEnter');
			},
			handlerLoading(res) {
				this.loading = res;
			}
		}
	}
</script>

<style lang="scss">
	@import './styles/base.scss';
	@import './styles/app.scss';
</style>
