import http from './axios'
import { api, get, post } from './api'

const Get = get;
const Post = post;
const request = {}

Get.map(item => {
	request[item] = (params, fn) => {
		http.get(
			window.API + api[item], params
		).then(response => {
			fn && fn(response);
		})
	}
})

Post.map(item => {
	request[item] = (params, fn) => {
		http.post(
			window.API + api[item], params
		).then(response => {
			fn && fn(response);
		})
	}
})

export default request