import Vue from 'vue'
import Router from 'vue-router'
import Main from './main'
import Home from './views/Home.vue'

Vue.use(Router)

const scrollBehavior = function(to, from, savedPosition) {
	return new Promise(resolve => {
		Main.$root.$once('scrollAfterEnter', () => {
			savedPosition && to.meta.isScroll ? resolve(savedPosition) : resolve({ x: 0, y: 0 });
		})
	})
}

const router = new Router({
	base: process.env.BASE_URL,
	linkActiveClass: 'active',
	linkExactActiveClass: 'active',
	scrollBehavior,
	routes: [
		{ path: '/', name: 'home', component: Home, meta: { title: '泰璞健康', isTabbar: true } },
		{ path: '/meal', name: 'meal', component: () => import('./views/Meal.vue'), meta: { title: '服务项目', isTabbar: true } },
		{ path: '/hospital', name: 'hospital', component: () => import('./views/Hospital.vue'), meta: { title: '合作医院', isTabbar: true } },
		{ path: '/example', name: 'example', component: () => import('./views/Example.vue'), meta: { title: '服务案例', isTabbar: true } },
		{ path: '/about', name: 'about', component: () => import('./views/About.vue'), meta: { title: '关于我们', isTabbar: true } },
		{ path: '/question', name: 'question', component: () => import('./views/Question.vue'), meta: { title: '常见问题', isScroll: true, isTabbar: true } },
		{ path: '/healthy', name: 'healthy', component: () => import('./views/healthy/Healthy.vue'), meta: { title: '小璞健康课堂', isScroll: true, isTabbar: false } },
		{ path: '/healthy-detail', name: 'healthy-detail', component: () => import('./views/healthy/HealthyDetail.vue'), meta: { title: '小璞健康课堂', isScroll: true, isTabbar: false } },
		{ path: '/meal-detail', name: 'meal-detail', component: () => import('./views/meal/MealDetail.vue'), meta: { title: '套餐详情', isScroll: true, isTabbar: false } },
		{ path: '/mdetail', name: 'mdetail', component: () => import('./views/meal/MDetail.vue'), meta: { title: '套餐详情', isScroll: true, isTabbar: false } },
		{ path: '/service', name: 'service', component: () => import('./views/detail/Service.vue'), meta: { isScroll: true, isTabbar: false } },
		{ path: '/redetail', name: 'redetail', component: () => import('./views/detail/Redetail.vue'), meta: { isScroll: true, isTabbar: false } },
		{ path: '/', redirect: '/home' },
	]
})

router.beforeEach((to, from, next) => {
	if (to.meta.title) {
		document.title = to.meta.title;
	}
	
	sessionStorage.tabbarVisible = to.meta.isTabbar;
	next();
})

export default router
