<template>
	<div class="list">
		<div class="list-item" :key="item.id" v-for="item in list" @click="changeItem(item)">
			<div class="list-meta">
				<div class="list-meta-title">{{item.title}}</div>
				<p class="list-meta-detail">{{item.description}}</p>
			</div>
			<div class="list-coevr">
				<img :src="item.headImg">
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: ['data', 'size', 'align'],
		data() {
			return {
				list: {}
			}
		},
		created() {
			this.list = this.data;
		},
		methods: {
			changeItem(item) {
				this.$emit('changeItem', item);
			}
		},
		watch: {
			data(newValue) {
				this.list = newValue;
			}
		}
	}
</script>

<style lang="scss">
	@import '@/styles/components/list.scss';
</style>