<template>
	<div class="re-meal">
		<div class="re-meal-item" v-for="item in sourceData" @click="changeItem(item)">
			<h3>{{item.title}}</h3>

			<div class="re-meal-item-body">
				<div class="re-meal-info">
					<div class="day-price">
						<div class="day">{{item.day}}</div>
						<div class="price">{{item.price}}万（RMB）起/人</div>
					</div>

					<div class="description">{{item.description}}</div>	
				</div>
				<div class="re-meal-avatar">
					<img :src="item.headImg">
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: ['data'],
		data() {
			return {
				sourceData: []
			}
		},
		methods: {
			changeItem(item) {
				this.$emit('changeReItem', item);
			}
		},
		watch: {
			data(newValue) {
				this.sourceData = newValue;
			}
		}
	}
</script>

<style lang="scss">
	@import '@/styles/components/recommend.scss';
</style>