<template>
	<div class="home">
		<Carousel :options="indexSwiperOption" :data="indexSwiperData" :type="'index'" @change="changeCarousel" />

		<div class="tp-artical tp-artical-server mb">
			<div class="header-name">
				<div class="zh">服务项目</div>
			</div>

			<Service :data="service" @changeService="changeService" />
		</div>

		<div class="tp-artical mb">
			<div class="header-name">
				<div class="zh">推荐套餐</div>
			</div>

			<Recommend :data="recommend" @changeReItem="changeReItem" />
		</div>

		<div class="tp-artical mb">
			<div class="header-name">
				<div class="zh">服务案例</div>

				<div class="more" @click="changeMore('example', true)">查看更多 ></div>
			</div>

			<Card :key="item.id" v-for="item in example" :data="item" @changeExample="changeExample" />
		</div>

		<div class="tp-artical">
			<div class="header-name">
				<div class="zh">小璞健康讲堂</div>

				<div class="more" @click="changeMore('healthy', false)">查看更多 ></div>
			</div>

			<Carousel :options="healthySwiperOption" :data="healthySwiperData" :type="'healthy'" @change="changeCarousel" />

			<List :data="healthyData" @changeItem="changeItem" />
		</div>
	</div>
</template>

<script>
	import Carousel from '@/components/Carousel.vue'
	import Service from '@/components/Service.vue'
	import Recommend from '@/components/Recommend.vue'
	import Setmeal from '@/components/Setmeal.vue'
	import Card from '@/components/Card.vue'
	import List from '@/components/List.vue'

	export default {
		name: 'home',
		components: { Carousel, Service, Recommend, Setmeal, Card, List },
		data() {
			return {
				indexSwiperOption: {
					slidesPerView: 'auto',
					// spaceBetween: 10,
					pagination: {
						el: '.swiper-pagination',
						clickable: true
					}
				},
				indexSwiperData: [],
				healthySwiperOption: {
					slidesPerView: 'auto',
					// spaceBetween: 10,
					pagination: {
						el: '.swiper-pagination',
						clickable: true
					}
				},
				healthySwiperData: [],
				healthyData: [],
				service: [],
				recommend: [],
				example: [],
				publicPath: process.env.BASE_URL
			}
		},
		created() {
			fnCarousel(this);
			fnService(this);
			fnRecommend(this);
			fnExample(this);
			fnHealthy(this);
			this.$emit('handlerTabbar', true);
		},
		methods: {
			changeMore(path, boolean) {
				this.$emit('handlerTabbar', boolean);
				this.$router.push({ path: path });
			},
			changeItem(item) {
				this.$router.push({ path: 'healthy-detail', query: { id: item.id }});
			},
			changeMeal(item) {
				this.$emit('handlerTabbar', false);
				this.$router.push({ path: 'meal-detail', query: { id: item.id }});
			},
			changeReItem(item) {
				this.$emit('handlerTabbar', false);
				sessionStorage.detail = JSON.stringify(item);
				this.$router.push({ path: 'redetail', query: { id: item.id }});
			},
			changeExample(item) {
				this.$emit('handlerTabbar', true);
				this.$router.push({ path: 'example', query: { id: item.id } });
			},
			changeCarousel(response) {
				if (response.type == 'index') {
					switch(response.index) {
						case 0:
							this.$router.push({ path: 'meal-detail', query: { id: 101 }});
							break;
						case 1:
							this.$router.push({ path: 'healthy'});
							break;
						case 2:
							this.$router.push({ path: 'example'});
							break;
					}
				}

				if (response.type == 'healthy') {
					this.$router.push({ path: 'healthy-detail', query: { id: this.healthySwiperData[response.index].id }});
				}
			},
			changeService(res) {
				this.$emit('handlerTabbar', false);
				sessionStorage.detail = JSON.stringify(res.item);
				this.$router.push({ path: 'service', query: { type: res.path, id: res.item.id }});
			}
		}
	}

	function fnCarousel(that, params) {
		that.$request.carousel(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.indexSwiperData = result.list.index;
				that.healthySwiperData = result.list.healthy;
			}
		})
	}

	function fnService(that, params) {
		that.$request.service(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.service = result.list;
			}
		})
	}

	function fnRecommend(that, params) {
		that.$request.recommend(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.recommend = result.list;
			}
		})
	}

	function fnExample(that, params) {
		that.$request.example(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.example = result.list;
			}
		})
	}

	function fnHealthy(that, params) {
		that.$request.healthy(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;

				result.list.map((item, index) => {
					if (index < 2) {
						that.healthyData.push(item);
					}
				});
			}
		})
	}
</script>

<style lang="scss">
	@import './../styles/home.scss'
</style>
