<template>
	<div class="carousel">
		<swiper ref="carousel" :options="swiperOption">
			<swiper-slide :key="index" v-for="(swiper, index) in swiperData">
				<img :src="swiper.headImg">
				<h3>{{swiper.title}}</h3>
				<div class="fight"></div>
			</swiper-slide>

			<div class="swiper-pagination" slot="pagination" v-if="swiperOption.pagination"></div>
		</swiper>
	</div>
</template>

<script>
	export default {
		props: ['options', 'data', 'type'],
		data() {
			return {
				swiperOption: {},
				swiperData: []
			}
		},
		created() {
			this.swiperOption = this.options;
			this.$set(this.swiperOption, 'on', {
				click: () => {
					let activeIndex = this.$refs['carousel'].swiper.activeIndex;
					this.$emit('change', { index: activeIndex, type: this.type });
				}
			});
			this.swiperData = this.data;
		},
		methods: {
			change(swiper) {
				console.log(swiper)
			}
		},
		watch: {
			options(newValue) {
				this.swiperOption = newValue;
			},
			data(newValue) {
				this.swiperData = newValue;
			}
		}
	}
</script>

<style lang="scss">
	@import '@/styles/components/carousel.scss';
</style>